var _self;
export default {
	data() {
		return {
			page_type: '1', //1修改姓名 2 修改密码
			title: '',
			user_name: '',
			old_password: '',
			new_password: '',
			angin_password: ''
		};
	},
	onLoad(e) {
		_self = this;
		_self.page_type = _self.$Route.query.page_type;
		_self.title = _self.$Route.query.msg;
		_self.user_name = _self.$Route.query.user_name;
		uni.setNavigationBarTitle({
			title: _self.title
		});
	},
	onNavigationBarButtonTap(e) {
		if(_self.page_type=='1'){
			_self.changeUserInfo()
		}
		if(_self.page_type=='2'){
			_self.changePassword()
		}
	},
	methods: {
		clearInput() {
			_self.user_name = '';
		},
		changeUserInfo() {
			_self.$ajax({
				path: _self.$api.profile,
				data:{
					nickname:_self.user_name
				},
				success(res) {
					let { data, code, msg } = res.data;
					uni.showToast({
						title: msg,
						icon: 'success',
						success() {
							setTimeout(()=>{
								_self.$common.setUserInfo(data,()=>{
									uni.hideToast()
									_self.$Router.back(1)
								})
							},600)
						}
					});
				}
			});
		},
		changePassword() {
			if(!_self.old_password||_self.old_password.length<6){
				uni.showToast({
					icon:"none",
					title:"当前密码至少为六位数"
				})
				return false;
			}
			if(!_self.new_password||_self.new_password.length<6){
				uni.showToast({
					icon:"none",
					title:"新密码至少为六位数"
				})
				return false;
			}
			if(!_self.angin_password||_self.angin_password.length<6){
				uni.showToast({
					icon:"none",
					title:"确认密码至少为六位数"
				})
				return false;
			}
			if(_self.new_password!==_self.angin_password){
				uni.showToast({
					icon:"none",
					title:"两次密码不一致"
				})
				return false;
			}
			_self.$ajax({
				path: _self.$api.profile,
				data:{
					old_pwd:_self.old_password,
					new_pwd:_self.new_password,
					is_new_pwd:_self.angin_password
				},
				success(res) {
					let { data, code, msg } = res.data;
					uni.showToast({
						title: msg,
						icon: 'success',
						success() {
							setTimeout(()=>{
								_self.$common.setUserInfo(data,()=>{
									uni.hideToast()
									_self.$Router.back(1)
								})
							},600)
						}
					});
				}
			});
		}
	}
};
